import Vue from 'vue'
import Router from 'vue-router'
import Meta from 'vue-meta'
import NotFound from '@/components/NotFound'
import globals from '@/globals'
import Layout2 from '@/layout/Layout2'

Vue.use(Router)
Vue.use(Meta)

const router = new Router({
  base: `/${process.env.VUE_APP_NOT_SECRET_CODE}`,
  mode: 'history',
  routes: [
    {
      path: '/',
      component: Layout2,
      children: [
        {
          path: '',
          component: () => import('@/pages/dashboard/Home')
        },
        {
          path: '/dashboard/configuracao',
          component: () => import('@/pages/dashboard/Config')
        },
        {
          path: '/agents/active-layer',
          component: () => import('@/pages/agents/ActiveLayer')
        },
        {
          path: 'agents/login',
          component: () => import('@/pages/agents/Login')
        },
        {
          path: 'agents/performance',
          component: () => import('@/pages/agents/Perfomance')
        },
        {
          path: 'agents/menu-status',
          component: () => import('@/pages/agents/MenuStatus')
        },
        {
          path: 'campaign/call-performance',
          component: () => import('@/pages/campaign/CallPerformance')
        },
        {
          path: 'campaign/detailed',
          component: () => import('@/pages/campaign/Detailed')
        },
        {
          path: 'general/abandoned',
          component: () => import('@/pages/general/AbandonedCallers')
        },
        {
          path: 'general/login',
          component: () => import('@/pages/general/Login')
        },
        {
          path: 'scripts'
        }
      ]
    },
    {
      // 404 Not Found page
      path: '*',
      component: NotFound
    }
  ]
})

router.afterEach(() => {
  // On small screens collapse sidenav
  if (
    window.layoutHelpers &&
    window.layoutHelpers.isSmallScreen() &&
    !window.layoutHelpers.isCollapsed()
  ) {
    setTimeout(() => window.layoutHelpers.setCollapsed(true, true), 10)
  }

  // Scroll to top of the page
  globals().scrollTop(0, 0)
})

router.beforeEach((to, from, next) => {
  // Set loading state
  document.body.classList.add('app-loading')

  // Add tiny timeout to finish page transition
  setTimeout(() => next(), 10)
})

export default router
