var store = {
  state: {
    title: null,
    tmr: 0,
    gns: 0,
    tmf: 0,
    txa: 0,
    agents: []
  },
  mutations: {
    setTmf (state, obj) {
      state.tmf = (obj) || null
    },
    setGns (state, obj) {
      state.gns = (obj) || null
    },
    setTxa (state, obj) {
      state.txa = (obj) || null
    },
    setTmr (state, obj) {
      state.tmr = (obj) || null
    },
    setTitle (state, obj) {
      state.title = (obj) || null
    },
    setAgents (state, obj) {
      state.agents = (obj) || null
    }
  }
}

export default store
