<template>
  <sidenav :orientation="orientation" :class="curClasses">

    <!-- Inner -->
    <div class="sidenav-inner" :class="{ 'py-1': orientation !== 'horizontal' }">
      <sidenav-menu icon="ion ion-md-apps" :active="isMenuActive('/dashboard')" :open="isMenuOpen('/dashboard')">
        <template slot="link-text">Painel de Controle</template>
        <sidenav-router-link icon="ion ion-md-analytics" to="/" :exact="true">Dashboard</sidenav-router-link>
        <sidenav-router-link icon="ion ion-md-settings" to="/dashboard/configuracao" :exact="true">Configuração</sidenav-router-link>
      </sidenav-menu>
  <sidenav-menu icon="ion ion-md-person" :active="isMenuActive('/agents')" :open="isMenuOpen('/agents')">
    <template slot="link-text">Agentes</template>
     <sidenav-router-link to="/agents/active-layer" :exact="true">Chamada Ativa p/ Tel.</sidenav-router-link>
     <sidenav-router-link to="/agents/performance" :exact="true">Desempenho</sidenav-router-link>
     <sidenav-router-link to="/agents/login" :exact="true">Login/Pausas Detalhado</sidenav-router-link>
     <sidenav-router-link to="/agents/menu-status" :exact="true">Menu Status</sidenav-router-link>
     </sidenav-menu>
  <sidenav-menu icon="ion ion-md-speedometer" :active="isMenuActive('/campaign')" :open="isMenuOpen('/campaign')">
    <template slot="link-text">Campanha</template>
     <sidenav-router-link to="/campaign/call-performance" :exact="true">Desempenho Chamada</sidenav-router-link>
     <sidenav-router-link to="/campaign/detailed" :exact="true">Detalhadas</sidenav-router-link>
    </sidenav-menu>
    <sidenav-menu icon="ion ion-md-apps" :active="isMenuActive('/general')" :open="isMenuOpen('/general')">
    <template slot="link-text">Geral</template>
    <sidenav-router-link to="/general/abandoned" :exact="true">Abandonadas</sidenav-router-link>
    <sidenav-router-link to="/general/login" :exact="true">Login/Pausas</sidenav-router-link>
    </sidenav-menu>
    </div>
  </sidenav>
</template>

<script>
import { Sidenav, SidenavLink, SidenavRouterLink, SidenavMenu, SidenavHeader, SidenavBlock, SidenavDivider } from '@/vendor/libs/sidenav'

export default {
  name: 'app-layout-sidenav',
  components: {
    /* eslint-disable vue/no-unused-components */
    Sidenav,
    SidenavLink,
    SidenavRouterLink,
    SidenavMenu,
    SidenavHeader,
    SidenavBlock,
    SidenavDivider
    /* eslint-enable vue/no-unused-components */
  },

  props: {
    orientation: {
      type: String,
      default: 'vertical'
    }
  },
  mounted () {
    this.toggleSidenav()
  },
  computed: {
    curClasses () {
      let bg = this.layoutSidenavBg

      if (this.orientation === 'horizontal' && (bg.indexOf(' sidenav-dark') !== -1 || bg.indexOf(' sidenav-light') !== -1)) {
        bg = bg
          .replace(' sidenav-dark', '')
          .replace(' sidenav-light', '')
          .replace('-darker', '')
          .replace('-dark', '')
      }

      return `bg-${bg} ` + (
        this.orientation !== 'horizontal'
          ? 'layout-sidenav'
          : 'layout-sidenav-horizontal container-p-x flex-grow-0'
      )
    }
  },

  methods: {
    isMenuActive (url) {
      return this.$route.path.indexOf(url) === 0
    },

    isMenuOpen (url) {
      return this.$route.path.indexOf(url) === 0 && this.orientation !== 'horizontal'
    },

    toggleSidenav () {
      this.layoutHelpers.toggleCollapsed()
    }
  }
}
</script>
